// @flow

import {createGlobalStyle} from 'styled-components/macro';
import {rgba, darken, lighten, capitalize, downcase} from '../utilities';
import {fluid, colors, spacing, font, page} from '../config/styles';

// font location: /static/fonts
// name format:   [family]-[weight] (family must be lowercased in filename)
function generateFontFaces(family: string, weights: Object): string {
    const familyLower = downcase(family);
    const mapped = [];

    Object.keys(weights).forEach(name => {
        if (['definitions', 'fmtVar', 'withVar'].includes(name)) return;

        const weight = weights[name];
        const localName = capitalize(name);

        mapped.push(`@font-face {
           font-family: ${family};
           font-style: normal;
           font-display: swap;
           font-weight: ${weight};
           src: url('/static/fonts/${familyLower}-${weight}.eot'); /* IE9 Compat Modes */
           src: local('${family} ${localName}'),
                local('${family}-${localName}'),
                url('/static/fonts/${familyLower}-${weight}.eot?#iefix') format('embedded-opentype'), /* IE6-IE8 */
                url('/static/fonts/${familyLower}-${weight}.woff2') format('woff2'), /* Super Modern Browsers */
                url('/static/fonts/${familyLower}-${weight}.woff') format('woff'), /* Modern Browsers */
                url('/static/fonts/${familyLower}-${weight}.ttf') format('truetype'), /* Safari, Android, iOS */
                url('/static/fonts/${familyLower}-${weight}.svg#${family}') format('svg'); /* Legacy iOS */
           }`);
    });

    return mapped.join();
}

export default createGlobalStyle`
    ${generateFontFaces(page.fontFamily, font.weights)}

    * {
        box-sizing: border-box;

        &::selection {
            background-color: ${lighten(colors.primary, 0.9)};
            color: ${darken(colors.primary, 0.3)};
        }
    }

    :root {
        ${colors.definitions};
        ${page.definitions};
    }

    html {${fluid('font-size', '0.8rem', '1.2rem')}}

    body {
        height: 100%;
        margin: 0;
        padding: 0;
        font-family: ${page.fontFamily}, serif;
        line-height: 1.6;
        font-weight: ${font.weights.light};
    }

    #__next {
        height: 100%;
        height: 100vh;
        ${page.withVar('color')};
    }

    article, aside, details, figcaption, figure, footer,
    header, main, mark, nav, section {
        display: block;
    }

    h1, h2, h3, h4, h5, h6 {
        ${colors.withVar('black', 'color')};
        line-height: 1.1;
        font-weight: ${font.weights.bold};
        margin: 0 0 ${spacing.small};
        ${fluid('margin-top', spacing.medium, spacing.large)}
    }

    h1 {${fluid('font-size', '1.5rem', '2.8rem')}}
    h2 {${fluid('font-size', '1.4rem', '2.3rem')}}
    h3 {${fluid('font-size', '1.3rem', '2.1rem')}}
    h4 {${fluid('font-size', '1.2rem', '1.8rem')}}
    h5 {${fluid('font-size', '1.1rem', '1.5rem')}}
    h6 {${fluid('font-size', '1rem', '1.2rem')}}

    p {
        font-size: 1.15rem;
        margin-top: 0;
        margin-bottom: ${spacing.medium};
    }

    li {
        font-size: 1.15rem;
        margin-bottom: ${spacing.medium};
    }

    blockquote {
        font-size: 1.15rem;
    }

    pre,
    code,
    kbd {
        padding: 0.5rem 1rem;
        border-radius: 4px;
        background-color: ${darken(colors.white, 0.05)};
        ${page.withVar('color')};
    }

    code,
    kbd {
        font-size: 0.8em;
        padding: 0.2rem 0.4rem 0.15rem;
        border-radius: 4px;
    }

    pre code {
        font-size: 0.9rem;
        padding: 0;
        background-color: transparent;
        display: block;
    }

    kbd {
        background-image: none;
        border: 1px solid ${rgba(colors.dimgray, 0.2)};
    }

    a, a > code {
        position: relative;
        ${colors.withVar('primary', 'color')};
        text-decoration: none;
        outline: none;

        &:not(:active):focus {
            text-shadow: -0.06ex 0 ${colors.primary}, 0.06ex 0 ${colors.primary};
        }
    }
`;
